/* Define colors for dark theme */
:root {
  --background-color-dark: #101010;
  --text-color-dark: #f2f2f2;
  --nav-pill-background-dark: rgba(242, 242, 242, .05);
  --nav-pill-border-dark: rgba(242, 242, 242, .1);
  --nav-pill-shadow-dark: rgba(0, 0, 0, .15);
}

/* Define colors for light theme */
:root.light-mode {
  --background-color-light: #f2f2f2;
  --text-color-light: #101010;
  --nav-pill-background-light: rgba(0, 0, 0, .05);
  --nav-pill-border-light: rgba(0, 0, 0, .1);
  --nav-pill-shadow-light: rgba(0, 0, 0, .15);
}

/* Default to dark theme */
body {
  background-color: var(--background-color-dark);
  color: var(--text-color-dark);
  transition: background-color 0.5s ease;
}

/* Light mode */
body.light-mode {
  background-color: var(--background-color-light);
  color: var(--text-color-light);
}

a {
  color: var(--text-color-dark);
  text-decoration: none; /* no underline */
}

.main {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  scroll-snap-align: start;
  scroll-snap-stop: always;
}

.main > div:not(:first-of-type) {
  margin: 2vh;
}

.main > div:first-child {
  height: 69vh;
}

.main > div:not(:first-of-type) {
  height: 100vh;
  color: var(--text-color-dark);
  font-family: "Plus Jakarta Sans", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
  font-weight: 500;
  font-size: xx-large;
}

.card {
  height: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.cardContent {
  color: var(--text-color-dark);
  font-family: "Plus Jakarta Sans", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
  font-weight: 700;
  font-size: 3.5rem;
}

.contactCard {
  color: var(--text-color-dark);
  font-family: "Plus Jakarta Sans", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
  font-weight: normal;
  font-size: medium;
  margin-top: 14px;
}

.contactCard > *:not(:first-child) {
  margin: 10px;
}

.custom-cursor {
  width: 70px;
  height: 70px;
  position: fixed;
  top: 0;
  left: 0;
  border-radius: 50%;
  pointer-events: none;
  backdrop-filter: invert(1) grayscale(1);
  -webkit-backdrop-filter: invert(1) grayscale(1);
  transition: transform 0.1s ease-out;
  transform: translate(-50%, -50%);
  z-index: 99999;
}

.nav-pill-wrapper {
  margin-top: 2vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: sticky;
  top: 0;
  padding: 16px;
}

.nav-pill {
  -webkit-backdrop-filter: blur(15px);
  backdrop-filter: blur(15px);
  background-color: var(--nav-pill-background-dark);
  border: 1px solid var(--nav-pill-border-dark);
  border-radius: 24px;
  justify-content: center;
  align-items: center;
  min-width: 300px;
  height: 48px;
  padding-left: 6px;
  padding-right: 6px;
  transition: box-shadow 1s cubic-bezier(.23, 1, .32, 1), border .6s cubic-bezier(.165, .84, .44, 1);
  display: flex;
  position: relative;
  box-shadow: 0 10px 25px var(--nav-pill-shadow-dark);
  color: var(--text-color-dark);
  padding: 0 10px 0 10px;
  pointer-events: auto;
}

.nav-pill:hover {
  background-color: rgba(242, 242, 242, .08);
  border-color: rgba(242, 242, 242, .2);
  box-shadow: 0 15px 40px rgba(0, 0, 0, .3);
  background-image: linear-gradient(270deg, rgba(0, 0, 0, 0), rgba(242, 242, 242, .05));
}

.nav-pill-content {
  font-size: 85%;
  --tw-text-opacity: 1;
  color: rgb(212 212 212 / var(--tw-text-opacity));
  font-weight: 500;
  flex: 1;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav-pill-content:hover {
  z-index: -1;
  background-color: rgba(242, 242, 242, .05);
  border-radius: 18px;
  width: 80px;
  height: 36px;
  text-align: center;
  color: white;
  text-decoration: solid;
  cursor: pointer;
  font-weight: bold;
}

.divider {
  --tw-border-opacity: 1;
  border-color: rgb(38 38 38 / var(--tw-border-opacity));
}

.background-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 2rem;
  font-weight: bold;
  font-family: Arial, Helvetica, sans-serif;
  color: rgba(255, 255, 255, 0.1); /* White with low opacity to appear greyed out */
  z-index: 0;
  text-align: center;
}
