.blogPage{
margin-left: 25vh;
margin-right: 25vh;
margin-top: 5vh;
min-height: 100vh;
font-family: Neue Montreal;
display: flex;
justify-content: center;
align-items: center;
flex-direction: column;
}

.blogInfo{
color: rgba(242, 242, 242, .4);
  font-family: Neue Montreal, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 125%;
  font-size: 18px;
}
.blogHeader{
    letter-spacing: .1px;
    flex: 0 auto;
    padding-right: 24px;
    font-family: Neue Montreal, sans-serif;
    font-size: xx-large;
    font-weight: 500;
    line-height: 125%;
    color: #f2f2f2;
    margin-top: 8px;
  margin-bottom: 9px;
  }

.blogSnap{
    color: rgba(242, 242, 242, .6);
  letter-spacing: .4px;
  flex: 1;
  font-family: Neue Montreal, sans-serif;
  font-size: 12px;
  font-weight: 300;
  line-height: 150%;
  font-size: 18px;
  letter-spacing: .4px;
}

.blog-divider{
    margin:4vh;
 width: 60%;
  height: 1px; /* Adjust thickness as needed */
  background-color: #848484;
}